<template>
  <div class="env">
    <div class="swiper-box" :key="activeTabs.name">
      <div class="info" >
        <h1 data-aos="fade-right" v-html="activeTabs.slideTitle"></h1>
        <p data-aos="fade-right" data-aos-delay="200" v-html="activeTabs.slideContent"></p>
        <img class="mask" src="@/assets/env/mask.png" alt="" srcset="" />
      </div>
      <swiper :options="swiperOptions">
        <swiper-slide
          v-for="(slide, i) in activeTabs.imgs"
          :key="i"
          v-bind:style="{ backgroundImage: `url('${slide}')` }"
        >
        </swiper-slide>
        <div class="swiper-button-prev" slot="button-prev">
          <i class="el-icon-caret-left"></i>
        </div>
        <div class="swiper-button-next" slot="button-next">
          <i class="el-icon-caret-right"></i>
        </div>
      </swiper>
    </div>
    <Nav :theme="'env-theme'" />
  </div>
</template>

<style lang="scss">
.env {
  .swiper-box {
    .swiper-button-prev,
    .swiper-button-next {
      color: #fff;
      &:after {
        font-size: size(60);
      }
    }

    .swiper-button-next {
      right: size(60);
    }

    .swiper-button-prev {
      left: size(60);
    }
  }
}
</style>

<style lang="scss" scoped>
.env {
  position: relative;
  overflow: hidden;

  .swiper-box {
    position: relative;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    .info {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 5;
      color: #fff;
      height: 100%;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      justify-content: center;
      margin-left: size(150);
      margin-top: -#{size(60)};
      letter-spacing: size(3);
      h1 {
        font-size: size(48);
        line-height: size(72);
      }
      p {
        font-size: size(24);
        line-height: size(48);
        font-weight: bold;
        max-width: size(360);
      }
      .mask {
        position: absolute;
        left: -#{size(150)};
        top: #{size(60)};
        height: 100%;
        z-index: -1;
      }
    }
    .swiper-container {
      .swiper-slide {
        width: 100%;
        height: 100vh;
        background-size: cover;
      }
    }
  }
}
</style>

<script>
import Nav from "../components/layout/nav.vue";
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'

import Swiper2, { Navigation, Pagination, Autoplay } from 'swiper';
Swiper2.use([Navigation, Pagination, Autoplay]);

export default {
  name: "Env",
  components: {
    Nav,
    Swiper,
    SwiperSlide
  },
  data() {
    return {
      swiperOptions: {
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      tabs: [
        {
          name: '',
          slideTitle: '<br>',
          slideContent: `
          <br/><br/>
          12<br/>20200.31%5610()`,
          imgs: [
            require('@/assets/env/1-1.jpg'),
            require('@/assets/env/1-2.jpg'),
          ]
        },
        {
          name: '',
          slideTitle: '<br/>',
          slideContent: `5A9600500UBIKE200`,
          imgs: [
            require('@/assets/env/2-1.jpg'),
            require('@/assets/env/2-2.jpg'),
          ]
        },
        {
          name: '',
          slideTitle: 'OUTLET<br/>',
          slideContent: `OUTLETOUTLET441310`,
          imgs: [
            require('@/assets/env/3-1.jpg'),
            require('@/assets/env/3-2.jpg'),
            require('@/assets/env/3-3.jpg'),
            require('@/assets/env/3-4.jpg'),
            require('@/assets/env/3-5.jpg'),
          ]
        },
        {
          name: '',
          slideTitle: '<br/>',
          slideContent: `AAIA `,
          imgs: [
            require('@/assets/env/4-1.jpg'),
            require('@/assets/env/4-2.jpg'),
          ]
        },
        {
          name: '',
          slideTitle: '<br/>',
          slideContent: `,311160`,
          imgs: [
            require('@/assets/env/5-1.jpg'),
            require('@/assets/env/5-2.jpg'),
            require('@/assets/env/5-3.jpg'),
          ]
        }
      ]
    }
  },
  computed: {
    activeTabs() {
      return this.tabs.find(item => item.name == this.$route.name)
    }
  },
  mounted() {
    this.$store.commit('toggleNavbar', true)
  }
};
</script>
