import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'

import Env from '../views/Env.vue'

import Product from '../views/Product.vue'
import ProductInfo from '../components/product/info.vue';
import ProductPlan from '../components/product/plan.vue';
import ProductTexture from '../components/product/texture.vue';

import Team from '../views/Team.vue'

Vue.use(VueRouter)

const routes = [{
        title: "",
        path: '/',
        name: 'Home',
        component: Home,
        children: []
    },
    {
        title: "",
        themeName: "env-theme",
        icon: require("@/assets/layout/env.svg"),
        path: '/env',
        name: 'Env',
        component: Env,
        children: [{
                title: "",
                name: "",
                path: '/env/1',
                component: Env
            },
            {
                title: "",
                name: "",
                path: '/env/2',
                component: Env
            },
            {
                title: "",
                name: "",
                path: '/env/3',
                component: Env
            },
            {
                title: "",
                name: "",
                path: '/env/4',
                component: Env
            },
            {
                title: "",
                name: "",
                path: '/env/5',
                component: Env
            }
        ]
    },
    {
        title: "",
        themeName: "team-theme",
        icon: require("@/assets/layout/team.svg"),
        path: '/team',
        name: 'Team',
        component: Team,
        children: [{
                title: "",
                name: "",
                path: '/team/1',
                component: Team
            },
            {
                title: "",
                name: "",
                path: '/team/2',
                component: Team
            },
            {
                title: "",
                name: "",
                path: '/team/3',
                component: Team
            },
            {
                title: "",
                name: "",
                path: '/team/4',
                component: Team,
            }
        ]
    },
    {
        title: "",
        themeName: "product-theme",
        icon: require("@/assets/layout/product.svg"),
        path: '/product',
        name: 'Product',
        component: Product,
        children: [{
                title: "",
                name: "",
                path: '/product/1',
                component: ProductInfo
            },
            {
                title: "",
                name: "",
                path: '/product/2',
                component: ProductPlan
            },
            {
                title: "",
                name: "",
                path: '/product/3',
                component: ProductTexture
            },
        ]
    }
]

const router = new VueRouter({
    //mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router